
import { Component, OnInit } from '@angular/core';
import { DirektoriPenilaianPelamarService } from 'src/app/sdk/hr/services';
import { Penilaian_pelamar } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPenilaianPelamarService: DirektoriPenilaianPelamarService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService
    ) { }

    id_pelamar: string = ''
    dataPenilaian: Penilaian_pelamar[] = []

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.getPengalaman()
    }


    getPengalaman() {
        this.spinner.show
        this.direktoriPenilaianPelamarService.getDirektoriPenilaianPelamars(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.dataPenilaian = data.data;
                this.spinner.hide()
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPenilaianPelamarService
            .deleteDirektoriPenilaianPelamarsId(id)
            .subscribe(data => {
                this.getPengalaman()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
