import { Component, OnInit } from '@angular/core';
import { DirektoriPenilaianPelamarService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Penilaian_pelamar, Pegawai_master } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPenilaianPelamarService: DirektoriPenilaianPelamarService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService
    ) { }

    toogleUpload = false
    id_pelamar: string = ''
    id_penilaian_pelamar: string = ''
    dataPenilaian: Penilaian_pelamar

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        }
        )
        this.id_penilaian_pelamar = this.activatedRoute.snapshot.paramMap.get('id_penilaian_pelamar')

        this.form()
        this.getPenilai()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'penilaian_pelamar.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    listPegawai: Pegawai_master[] = []
    getPenilai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data
            }
        )
    }

    getData() {
        this.spinner.show()
        this.direktoriPenilaianPelamarService.getDirektoriPenilaianPelamarsId(this.id_penilaian_pelamar).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "penilaian_pelamar",
                    for_table: "erp_hr_pelamar.penilaian_pelamar",
                    for_module_id: data.data.id_penilaian_pelamar
                })
            }
        )
    }
    dataForm: FormGroupTypeSafe<Penilaian_pelamar>
    form() {
        this.dataForm = this.fb.group<Penilaian_pelamar>({
            id_pegawai_penilai: new FormControl('', [Validators.required]),
            tahap_test: new FormControl(''),
            tanggal_test: new FormControl(''),
            nama_test: new FormControl(''),
            alasan: new FormControl(''),
            kesimpulan: new FormControl(''),
            test_berikutnya: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
            id_dokumen: new FormControl(this.id_pelamar),
            id_user: new FormControl(this.id_pelamar),
            created_at: new FormControl(this.id_pelamar),
            updated_at: new FormControl(this.id_pelamar),
            id_penilaian_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPenilaian = this.dataForm.value
        this.direktoriPenilaianPelamarService
            .putDirektoriPenilaianPelamarsId({ id: this.id_penilaian_pelamar, body: this.dataPenilaian })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'penilaian', 'list'])
    }

}
